@import 'imports/bootstrap-custom';
@import 'imports/vendor/bootstrap/bootstrap';

#portfolio-header {
  height: 6vw;
  background-color: $gray-900;
  transition: all 0.2s ease-in-out;

  .to-intro {
    position: fixed;
    z-index: 10;
    display: block;
    width: 5vw;
    height: 5vw;
    padding: 0.5vw 1.3vw 0;
    margin: 0 auto;
    position: relative;
    top: 2.7vw;
    color: transparentize($white, 0.2);
    background-color: $gray-800;
    border: 0.1vw solid $gray-900;
    border-radius: 50%;
    outline: 0;
    text-decoration: none;
    transform-origin: center top;
    transition: all 0.2s ease-in-out;

    .icon {
      height: 100%;
      position: relative;
      top: -0.3vw;
      transform: scaleY(-1);
    }

    &:hover {
      transform: scale(1.1);
    }
  }
}



